.profile_information_container {
    padding: 10px;
    display: flex;
}

.user_name {
    margin: 0 0 20px 0;
    font-size: 18px;
    font-weight: bold;
}

.user_information {
    margin: 0;
    font-size: 13px;
}

.ava_container {
    margin-right: 40px;
    height: 100px;
    width: 100px;
}

.ava_container img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
}




